<template>
  <v-app>
    <v-img
      src="http://ashewa.com/static/img/bg-mobile-fallback.jpg"
      class="align-end"
      gradient="to bottom left, #8bff8b59, rgb(31 48 27 / 72%)"
      :aspect-ratio="12 / 2"
    >
      <div class="mx-5">
        <v-col class="white--text dopeStyle">
          <h1 style="font-size: 42px">Ashewa</h1>
          <h2 style="font-size: 17px">
            <span style="font-size: 20px" class="px-1">Coming Soon!</span> We
            are coming with a brand new ashewa platforms, Stay Tight.
          </h2>
          <v-btn
            dark
            color="green darken-3 dopeStyle font-weight-bold"
            depressed
            @click="popUp"
            ><v-icon>mdi-download</v-icon> Download Ashewa Document</v-btn
          >
        </v-col>
        <v-col cols="12" lg="4" class="white--text my-5">
          <h2 class="dopeStyle" style="font-size: 30px">CONTACT US</h2>
          <v-form ref="form" v-model="valid" lazy-validation>
            <v-text-field
              dark
              v-model="fullName"
              class="mt-3"
              filled
              :rules="nameRules"
              outlined
              placeholder="Full Name"
            ></v-text-field>
            <v-text-field
              dark
              :rules="emailRules"
              v-model="email"
              filled
              outlined
              placeholder="Email Address"
            ></v-text-field>
            <v-textarea
              v-model="message"
              filled
              dark
              :rules="messageRules"
              outlined
              placeholder="Message"
            >
            </v-textarea>
          </v-form>
          <v-btn
            @click="submitMessage"
            large
            dark
            :loading="loading"
            color="green darken-3"
            width="250"
            >Submit</v-btn
          >
        </v-col>
        <v-spacer class="mb-15"></v-spacer>
      </div>
    </v-img>
  </v-app>
</template>
<style lang="scss">
@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Lora&display=swap");
.dopeStyle {
  font-size: 45px;
  font-family: "Montserrat", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
</style>
<script>
import gql from "graphql-tag";

const SEND_MESSAGE = gql`
  mutation userMessage($email: String!, $fullName: String!, $message: String!) {
    userMessage(email: $email, fullName: $fullName, message: $message) {
      payload
    }
  }
`;
const GET_CORE = gql`
  query {
    getCoreDocs {
      doc
    }
  }
`;
export default {
  data() {
    return {
      loading: false,
      valid: false,
      fullName: "",
      email: "",
      message: "",
      nameRules: [
        (v) => !!v || "Name is required",
        (v) => (v && v.length <= 50) || "Name must be less than 50 characters",
      ],
      messageRules: [(v) => !!v || "Message is required"],
      emailRules: [
        (v) => !!v || "E-mail is required",
        (v) => /.+@.+\..+/.test(v) || "E-mail must be valid",
      ],
      link: "",
    };
  },
  mounted() {
    this.$apollo
      .query({
        query: GET_CORE,
      })
      .then(({ data }) => {
        this.link = `http://ashewa.com/media/${data.getCoreDocs[0].doc}/`;
      })
      .catch((e) => {
        console.error(e);
      });
  },
  methods: {
    popUp() {
      window.open(
        this.link,
        // "http://127.0.0.1:8000/media/core/docs/ashewa_technology_solution_s.c1info.pdf",
        true
      );
    },
    submitMessage() {
      if (this.$refs.form.validate()) {
        this.loading = true;
        this.$apollo
          .mutate({
            mutation: SEND_MESSAGE,
            variables: {
              fullName: this.fullName,
              email: this.email,
              message: this.message,
            },
          })
          .then(({ data }) => {
            this.loading = false;
            if (data.userMessage.payload) {
              alert("Thanks for your reponse");
              this.$refs.form.reset();
            }
          })
          .catch((e) => {
            this.loading = false;
            console.error("some thing happened");
            console.error(e);
          });
      } else {
        return;
      }
    },
  },
};
</script>